html {
  background-color: #FFFFFF;
  margin: 0px;  /* this affects the margin on the html before sending to printer */
  margin-left: 4rem;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.test-element {
  width: 210mm;
  height: 297mm;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: start;
  align-items: start;
  align-content: flex-start;
  overflow: hidden;
}

.test-element_box {
  min-width: 10mm;
  width: 10mm;
  height: 10mm;
}

.test-element_box:nth-child(2n + 1) {
  background-color: #000000
}

.test-element_box:nth-child(2n) {
  background-color: #ffffff
}

@media print {
  .pagebreak {
    clear: both;
    page-break-after: always;
  }
}

body {
  size: A4 portrait;
  margin: 0;
}

body {
  size: 4mm 4mm;
  margin: 0;
}

.page-box-wrapper {
  overflow: hidden;
  height: 500px;
  width: 500px;
}

.page-box {
  /* padding: 25%; */
  margin-top: -200px;
  margin-left: -100px;
  transform: scale(0.5, 0.5) translate(0%, 0%)
}
